.header-contacts {
    position: relative;
    width: 850px;
    height: 100%;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background: url('../img/furniture-bg.jpg');
    background-size: cover;
    overflow: hidden;
    p, a {
        display: block;
        color: white;
        width: max-content;
        text-shadow: 5px 5px 5px black;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 400;
        font-size: 20px;
    }
    a {
        padding: 5px 20px;
        &:hover {
            color: gold;
        }
    }
    p {
        padding: 20px;
        cursor: default;
    }
}
.bg-darker {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    padding: 25px;
}

// -----------------------------------------------------------'

.wr-popular {
    width: 100%;
}
.popular-all {
    background: white;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    padding: 10px 0;
}
.popular-head {
    display: flex;
    flex-direction: row;
    align-items: center;
    h2 {
        font-size: 25px;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 400;
        padding: 0 25px;
    }
    img {
        width: 50px;
        height: 50px;
    }
}
.popular__grid-container {
    position: relative;
    display: block;
    margin: 10px auto;
    width: 320px;
    height: 340px;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    transition: 0.1s ease-in-out;
    &:hover {
        cursor: pointer;
        background: rgba(0, 0, 0, 0.1);
        box-shadow: 2px 2px 5px 2px rgba(255, 217, 0, 0.5);
    }
}
.popular__grid-background {
    display: block;
    margin: 5px auto;
    width: 300px;
    height: 320px;
    background: url("../img/furniture-bg.jpg");
    background-size: cover;
}
.popular__grid-discr {
    position: absolute;
    top: 300px;
    left: 35px;
    width: 250px;
    height: 80px;
    background: gold;
    border-radius: 10px;
    h3 {
        text-transform: uppercase;
        font-size: 18px;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 600;
        text-align: center;
        padding: 10px;
        text-shadow: 1px 1px 1px white;
    }
    p {
        font-size: 14px;
        font-family: "Open Sans",Helvetica,sans-serif;
        text-align: center;
        padding: 0 5px;
        text-shadow: 1px 1px 1px white;
    }
}
.popular-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 400px 400px;
}

// ------------------------------------------------------------------

.wr-gritings {
    margin: 20px 0;
    div {
        background: white;
        box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        padding: 20px 0;
    }
    h2 {
        padding: 10px 20px;
        font-size: 25px;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 600;
    }
    p {
        padding: 5px 20px;
        font-size: 18px;
        font-family: "Open Sans",Helvetica,sans-serif;
    }
    span {
        font-size: 18px;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 600;
        &:hover {
            cursor: pointer;
            color: gold;
        }
    }
}

// ---------------------------------------------------------------------

.wr-why-us {
    width: 100%;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    background: black;
    div {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
    }
    .why-us__container {
        display: flex;
        flex-direction: column;
        width: 320px;
        color: white;
        text-align: center;
        padding: 70px 20px 20px;
        h2 {
            position: relative;
            font-size: 22px;
            font-family: "Open Sans",Helvetica,sans-serif;
            font-weight: 600;
            padding: 10px 0;
        }
        &:nth-of-type(1) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 135px;
            background: url("../img/wu-sale.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        &:nth-of-type(3) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 135px;
            background: url("../img/wu-easy.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        &:nth-of-type(2) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 135px;
            background: url("../img/wu-hand.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        p {
            font-size: 17px;
            font-family: "Open Sans",Helvetica,sans-serif;
        }
    }
}

// -----------------------------------------------------------------------

.down-contacts {
    display: block;
    width: 850px;
    height: 100%;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background: white;
}
.down-contacts {
    ::-webkit-inner-spin-button, 
    ::-webkit-outer-spin-button {
        appearance: none;
    }
    h2 {
        text-align: center;
        padding: 50px 0 20px;
        font-size: 25px;
        font-family: "Open Sans",Helvetica,sans-serif;
        font-weight: 600;
    }
    form {
        width: 320px;
        display: flex;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 30px;
    }
    input,
    label {
        text-align: center;
        padding: 10px 0;
        font-size: 15px;
        font-family: "Open Sans",Helvetica,sans-serif;
        width: 100%;
        margin: 5px 0;
    }
    input {
        border: 1px solid black;
        box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
    }
}
.submit {
    background: gold;
    &:hover {
        box-shadow: 2px 2px 5px 2px rgba(255, 217, 0, 0.5);
    }
}

// -------------------------------------------------------------

footer {
    div {
        background: white;
        box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        padding: 20px 0;
    }
    h4,
    p,
    a {
        display: block;
        text-align: center;
        padding: 10px;
        font-size: 15px;
        font-family: "Open Sans",Helvetica,sans-serif;
    }
    a:hover {
        color: gold;
    }
}